//@media(max-width: 1024px) {

  $susy: (
    columns: 12,
    gutters: .25,
    gutter-position: inside,
  );
  @include layout($susy);

  %buttonBase{
    background-image: none;
    background-color: $cyan;
    background-repeat: no-repeat;
    vertical-align: top;
    width: 32%;
    height: 40px;
    margin-right: .5%;
    border-radius: 3px;
    border:1px solid darken($cyan, 7%);
    color: #fff;
    font-size: .8rem;
    font-weight: normal;
    text-transform: uppercase;
    cursor: pointer;
    &.active,
    &:hover {
      background-image: none;
      background-color: $magenta;
      border:1px solid darken($magenta, 7%);
    }
  }

  %buttonBaseAfter{
    color: $white;
    text-transform: uppercase;
        padding: 10px 0;
  }

  //----------------------------------------------------------------------------
  //                                LAYOUT
  //----------------------------------------------------------------------------
  html { box-sizing: border-box; }
  * { box-sizing: inherit; }
  @include border-box-sizing;

  body {
    width: 100%;
    margin: 0 auto;
    font-family: $font-global;
    background-color: $white;
    color: $text;
    font-size: 1em;
    line-height: 1.2em;
    font-size: .9rem;
    //overflow-x: hidden;
  }

  #main {
    width: auto;
    margin: 0 auto;
  }

  // header --------------------------------------------------------------------

  #header {
    @include container (100%);
    width: 100%;
    height: 300px;
    position: relative;
    margin: 0 auto;
    text-align: left;
    z-index: 100;
    background: url('../images/desktop.png') no-repeat;
    background-repeat: no-repeat;
    background-position: center top;
    background-size: contain;
  }

  #content {
    position: relative;
    width: 100%;
    margin: 0 auto;
    background: none;
    top: 0;
  }

  #content_bg_top {
    @include container (100%);
    width: 100%;
    min-height: 400px;
    height: auto !important;
    height: 755px;
    padding: 0 5% 15px;
  }

  // Footer --------------------------------------------------------------------

  #main2 {
    @include container (100%);
    margin-top: 0;
    background: #fff;
    clear: both;
    height: auto;
    width: 100%;
  }

  #footer {
    position: relative;
    //width: 706px;
    @include container (100%);
    width: 100%;
    margin: 0 auto;
    background: none;
    height: auto;
    padding: 25px 5% 0;
  }

  #footer2 {
    @include container (90%);
    position: relative;
    width: 100%;
    margin: 0auto;
    height: auto;
    padding: 0;
    //border: 2px solid $color_21;
    //background-color: $color_22;
  }

  #footer3 {
    @include container (100%);
    border-top:10px solid darken($text,10%);
    position: relative;
    width: 100%;
    height: 100px;
    margin: 20px auto 0;
    background: $cyan;
    padding: 10px 0 100px;
    font-size: .9rem;
    text-align: center;
    color: $white;
    a{
      color: $white;
      font-weight: bold;
      text-transform: uppercase;
    }
  }

  #copy {
    position: relative;
    @include container (100%);
    top: 0;
    left: auto;
    right: auto;
    width: 100%;
    height: auto;
    text-align: center;
    margin: 0 auto;
    font-family: $font-special;
    p{
      margin-bottom: 0;
    }
    .copyright{
      position: relative;
      bottom: 0;
      left: 0;
      margin: 10px auto 0;
      padding-top: 30px;
      padding-bottom: 30px;
      width: 100%;
      display: block;
      background: darken($text,10%);
      color: $white;
      text-align: center;
      font-size: 1.2rem;
      letter-spacing: 2px;
      a {
        color: $white;
      }
    }
  }

  .footer-keywords {
    display: none;
  }

  #under_footer {
    position: relative;
    display: block;
    width: 100%;
    margin: 0 auto;
    height: 45px;
    padding: 14px 45px 0;
    text-align: center;

    a {
      &:link,
      &:visited {
        color: $color_4;
        font-size: 10px;
        line-height: 12px;
        text-decoration: none;
      }

      &:hover {
        color: $color_5;
      }
    }
  }

  #mainnew,
  #mainnew2 {
    display: none;
    p {
      width: 100%;
    }
  }

  //----------------------------------------------------------------------------
  //                        DECLARATIONS GENERALES
  //----------------------------------------------------------------------------

  .split,
  .splitorhide{
    display: inline-block;
  }

  .small {
    font-size: .75rem;
  }

  img {
    border: 0;
  }

  dd {
    margin-left: 10px;
  }

  hr {
    position: relative;
    float: none;
    display: block;
    width: 10px;
    margin: 3px auto;
    height: 1px;
    background: transparent;
    border: none;
    line-height: 1px;

    &.contact {
      position: relative;
      float: none;
      display: inline;
      width: auto;
      margin: 3px 0 11px;
      height: 1px;
      background: $text;
      border: none;
      line-height: 1px;
    }

    &.bottom_hr {
      display: none;
    }
  }

  .aligncenter {
    margin: 0 auto;
  }

  .alignleft {
    float: left;
  }

  .alignright {
    float: right;
  }

  .maj{
    font-weight: 900;
  }

  p {
    margin-top: 0;

    &.sujet {
      margin: 10px 0;
      width: auto;
    }
  }

  small {
    font-size: 0.8rem;
    line-height: 1rem;
  }

  h1, h2 {
    font-size: 1.1rem;
    line-height: 1.4rem;
    margin: 15px 0;
    font-style: italic;
    font-weight: bold;
    color: $text;
    font-family: $font-title;

    &.categorytitle {
      margin: 0;
    }
  }

  .h1product {
    display: none;
  }

  h5 {
    font-family: $font-title;
    padding: 0;
    margin: 0;
  }

  h6 {
    font-family: $font-title;
  }

  h4 {
    color: $jaune;
    margin: 20px auto 10px;
    padding: 0;
    font-size: 1rem;
    font-weight: bold;
    font-family: $font-special;
    line-height: 1rem;
    text-transform: uppercase;
    text-align:left;


    &.clients_reviews_titre {
      width: 100%;
      background: $jaune;
      color:$white;
      padding: .7rem;
      margin-top: 0;
      margin-bottom: 0;
      text-align: center;
    }
  }
  .star-note{
    display: block;
  }

  .hide {
    visibility: hidden;
  }

  .floatLeft {
    float: left;
  }
  .floatLeftMg {
    float: left;
    margin-right: 10px;
    vertical-align: top;
  }

  .floatRight {
    float: right;
  }


  .highlight {
    color: $alert;
    font-weight: 700;
  }
  a .highlight{
    color: $alert;
    font-weight: 900;
    text-decoration: none;
  }

  .highlight-alt1 {
    color: $cyan;
  }

  .disMob0 {
    display: none;
  }

  form select {
    height: 40px;
    border: 1px solid $color_7;
    border-radius: 3px;
    padding-left: 10px;
    color: $text;
    font-size: .8rem;
  }
  input[type="submit"]{
    @include buttonLog($cyan, 150px);
    height: 40px;
    margin-top: 9px;
  }

  // blocs droite gauche

  .bloc-left{
    width: 49%;
    margin-right:2%;
    float: left;
  }
  .bloc-right{
    width: 49%;
    float: right;
  }

  .pink_link{
    color: $magenta;
  }

  //----------------------------------------------------------------------------
  //                                LINKS
  //----------------------------------------------------------------------------

  a {
    &:link {
      color: $cyan;
      font-weight: bold;
      text-decoration: none;
    }

    &:visited {
      color: $text;
      text-decoration: none;
    }

    &:hover {
      color: $color_5;
    }

    &.invisible_link {
      &:link {
        color: $white;
      }

      &:visited {
        color: $white;
      }

      &:hover {
        color: $color_5;
      }
    }

    &.pink_link {
      &:link {
        color: $magenta;
        text-transform: none;
      }

      &:visited {
        color: $magenta;
        text-transform: none;
      }

      &:hover {
        color: $color_5;
      }
    }



    &.conditio {
      &:link {
        color: $magenta;
      }

      &:visited {
        color: $magenta;
      }

      &:hover {
        color: $color_5;
      }
    }

    &.lcomment {
      &:hover,
      &:link,
      &:visited {
        width: 22px;
        height: 22px;
        position: absolute;
        left: 200px;
        top: 0;
        background: url('../images/lastcomment.png') no-repeat;
      }
    }

    &.lastcommentlink {
      &:link,
      &:visited {
        position: relative;
        margin-top: 10px;
        float: right;
        display: inline;
      }
    }

    &.pytajnik {
      position: absolute;
      right: 25px;
      top: 16px;
      width: 15px;
      height: 15px;
      background: url("../images/question.png") no-repeat;
      text-decoration: none;
    }

    &:active {
      outline: none;
    }

    &.linkUppercase {
      text-transform: uppercase;
      text-decoration: none;
    }
  }

  //----------------------------------------------------------------------------
  //                             MODULES
  //----------------------------------------------------------------------------

  // Header --------------------------------------------------------------------

  // module logo
  a#logo {
    position: absolute;
    top: 60px;
    left: 20px;
    width: 25%;
    height: 240px;
    background: url('../img/logo.png') top left no-repeat;
    background-size: contain;
  }

  // module titre site
  .site-head{
    width: 72%;
    position: relative;
    display: block;
    top: 95px;
    left: 24%;
    font-family: $font-special;
    letter-spacing: 1px;
    .site-title{
      font-size: 2.6rem;
      line-height: 2.6rem;
      &:after{
        content: '';
        background: url('../img/ligne-titre.png') top left repeat-y;
        display: block;
        width: 99%;
        height: 5px;
        margin: 0 0 0 2px;
      }
    }
    .site-subtitle{
      color: $text;
      text-align: left;
      padding: 0 5px 0 0;
      font-size: 1.2rem;
      line-height: 1.4rem;
      margin: 2px 0 0 2px;
    }
  }

  // module accès client
  ul.menu-client{
    @include container (55%);
    font-size: .7rem;
    line-height: 1.4rem;
    top:110px;
    left:20%;
    height:80px;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: transparent;
    list-style: none;
    font-family: $font-special;

    li{
      text-align:left;
      height: 60px;
      border-right:1px solid $jaune;
      &:last-of-type{
        border-right:none;
      }
    }
    .menu-client-item {
      @include span(3);
      text-align: center;
    }
    .menu-client-item.item1{
      @include span(6);
      text-align:left;
    }
   .menu-client-icon {
      display: block;
      font-size: 2.8rem;
      color: $cyan;
    }
    .menu-client-icon.phone {
      @include span (1 of 5);
      padding-right: 10px;
      padding-top: 10px;
      padding-left: 10px;
    }
    .menu-client-phone{
      @include span (4 of 5);
      box-sizing: border-box;
      padding-left: 15px;
    }
    a {
      text-decoration: none;
    }
    .tel1{
      font-size: 1rem;
      &:after{
        position: relative;
        left: 0;
        display: block;
        content: '';
        width: 100%;
        height:5px;
        background: url('../images/lignetel.png') repeat-y;
        margin-bottom: 2px;
      }
    }
    .tel2 {
      font-size: 1.3rem;
      a:hover{
        color: $magenta;
        transition-duration: .5s;
      }
    }
    .tel3 {
      font-size: .7rem;
      line-height: .7rem;
      text-align: right;
    }
  }

  // couleur des icones

  a .fa-lock{
    color: $jaune;
    &:hover {
      color: $cyan;
      transition-duration: .5s;
    }
  }
  .menu-client-icon a{
    color: $magenta;
    &:hover {
      color: $cyan;
      transition-duration: .5s;
    }
  }
  .menu-client-icon.phone a{
    color: $cyan;
    &:hover {
      color: $magenta;
      transition-duration: .5s;
    }
  }

  // module menu top

  #responsive-menu-container li.responsive-menu-item a{
    text-transform: uppercase;
  }

  ul {
    padding-left: 0;
    margin-top: 0;
    margin-left: auto;
    margin-right: auto;
    list-style-type: none;

    &#menu_top {
      width:75%;
      position: absolute;
      //width: 708px;
      height: 40px;
      top: 0;
      left: 0;
      text-transform: uppercase;
      font-weight: bold;
      font-size: 1rem ;
      background-color: transparent;
      li {
        position: relative;
        float: none;
        display: inline-block;
        height: 40px;
        margin: 0 auto;
        z-index: 1;
        text-align: center;
        vertical-align: middle;
        width:24%;
        a {
          &:link,
          &:visited {
            padding: 10px auto;
            display: block;
            font-size: .8rem;
            line-height: 22px;
            color: $text;
            text-align: center;
            text-decoration: none;
            width: 100%;
            height: 40px;
            border-left: 1px solid $bloc-base;
            padding-top: 1rem;
          }

          &#active,
          &:hover {
            background: transparent;
            color:$jaune;
          }
        }
      }

      ul {
        padding: 0;
        margin: 0;
        position: absolute;
        top: 40px;
        left: 0;
        z-index: 2;
        padding-top: 0;
        visibility: hidden;

        li {
          display: block;
          width: 250px;
          height: 32px;
          margin-left: 0;
          &.highest {
            display: block;
            width: 240px;
            height: 32px;
            // à supprimer
            a#active_sub {
              background-color: lighten($jaune, 10%);
              color: $white;
            }
          }
        }
      }

      a {
        &.menu_sub {
          &:link {
            display: block;
            width: 250px;
            height: 32px;
            background: $bloc-base;
            padding-top: 5px;
            text-decoration: none;
          }



          &:hover {
            background-color: lighten($jaune, 10%);
            color: $white;
          }
        }

        &#active_sub {
          background-color: lighten($jaune, 10%);
          color: $white;
        }
      }
    }
  }


  //----------------------------------------------------------------------------
  //                      MODULES PAGE ACCUEIL
  //----------------------------------------------------------------------------

  // module icones tarifs
  .b-it1{
    margin-right: 1%;
  }
  .b-it2 {
    margin-right: 1%;
  }
  .b-it3 {
    margin-right: 0;
  }

  ul#tarifs {
    float: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    list-style-type: none;
    margin: 0;
    padding: 0;

    li {
      width: 24%; //120
      height: auto; //160
      margin-bottom: 0;

      .home-buttons{
        width: 100%;
        height: auto;
      }



      &.separator_con {
        display: none;
      }

      .micro a {
        display: block;
        box-sizing: border-box;
        width: 100%;
        background: $text;
        font-size: .85rem;
        line-height: .87rem;
        letter-spacing: 1px;
        text-decoration: none;
        text-transform: uppercase;
        font-family: $font-special;
        color: #fff;
        font-weight: 700;
        margin-top: -15px;
        padding: 14px 2px 0;
        height: 45px;
        vertical-align: middle;
        .micro-s {
          display: block;
          font-size: .65rem;
          font-family: $font-global;
        }
        &:hover {
          background: $magenta;
        }
      }
    }
  }

  // module banières

  #banners {
    position: relative;
    clear: both;
    float: none;
    width: 100%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 50px;

    .banner1, .banner2  {
      float: none; // del
      width:49%;
      margin-bottom: 10px;
      display: block;
      height: auto;
    }
  }

  // module texte mots clés

    small {
      font-size: .9rem;
      font-weight: bold;
      line-height: 1.1rem;
      color: lighten($text, 20%);
      a{

        text-decoration: none;
      }
    }


  .mc_texte {
    padding: 10px 0px;
    font-size: .7rem;
    line-height: 1.2em;
  }

  // module paragraphe important
  .important {

    font-family: $font-special;
    color: $text;
    font-size: .8rem;
    margin-bottom: 20px;
    h3 {
      margin-bottom: 5px;
      font-size: .9rem;
    }
  }

  hr.hr-separator {
    clear: both;
    float: none;
    display: block;
    height: 1px;
    width: 50%;
    background: $jaune;
    margin: 10px auto;
  }

  .toggle-button {
    display: block;
    margin: 10px auto;
    cursor: pointer;
    text-align: center;
    border: 0;
    background-color: transparent;
    color: $jaune;
    font-weight: bold;
    font-family: $font-special;
    font-size: 1.2rem;
  }

  .toggle-block {
    display: none;
    font-family: $font-global;
    font-size: .85rem;
    line-height: 1rem;
    font-weight: normal;
  }


