  // UPLOAD --------------------------------------------------------------------

  // module upload

  SPAN {
    &#uploadfile_completedMessage {
      float: left;
      display: inline;
      margin-top: 2px;
    }

    &#uploadfile_uploading {
      float: left;
      display: inline;
      margin-left: 10px;
      margin-top: 2px;
    }

    &.asyncUploader OBJECT {
      position: relative;
      top: 2px;
      left: 10px;
    }
  }

  .fileinput-button {
    position: relative;
    overflow: hidden;
    float: left;
    margin-right: 5px;
  }

  .fileinput-button input {
    position: relative;
    top: 0;
    right: 0;
    margin: 0;
    opacity: 0;
    filter: alpha(opacity=0);
    transform: translate(-300px, 0) scale(4);
    font-size: 23px;
    direction: ltr;
    cursor: pointer;
  }

  .fileupload-buttonbar .ui-button,
  .fileupload-buttonbar .toggle {
    float: 0;
    margin-bottom: 10px;
    margin-left: 0;
  }

  .fileupload-buttonbar .toggle {
    display: none;
  }

  .files .progress {
    width: 100%;
  }

  .files .progress,
  .fileupload-buttonbar .progress {
    height: 20px;
  }

  .files .ui-progressbar-value,
  .fileupload-buttonbar .ui-progressbar-value {
    background: url(../img/progressbar2.gif);
  }

  .fileupload-buttonbar .fade,
  .files .fade {
    display: none;
  }

  .fileupload-loading {
    position: absolute;
    left: 50%;
    width: 128px;
    height: 128px;
    background: url(../img/loading.gif) center no-repeat;
    display: none;
  }

  .fileupload-processing .fileupload-loading {
    display: block;
  }

  .fileupload-progress {
    position: relative;
    display: block;
    margin: 10px 0 0 0;
    display: none;
    padding: 0 14px 0 14px;
    height: 20px;
    float: left;
    width: 600px;
  }

  .ui-progressbar {
    text-align: left;
    overflow: hidden;
    background: #fff;
    width: 100px;
    height: 10px;
    float: left;
    display: block;
    margin: 0 20px 0 0;
  }

  .ui-progressbar .ui-progressbar-value {
    margin: -1px;
    height: 100%;
  }

  .ui-progressbar .ui-progressbar-overlay {
    background: url("images/animated-overlay.gif");
    height: 100%;
    filter: alpha(opacity=25);
    opacity: 0.25;
  }

  .ui-progressbar-indeterminate .ui-progressbar-value {
    background-image: none;
  }

  .ui-widget-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .ui-widget {
    font-family: Verdana,
    Arial,
    sans-serif/*{ffDefault}*/
    ;
    font-size: 1.1em/*{fsDefault}*/
    ;
  }

  .ui-widget .ui-widget {
    font-size: 1em;
  }


